.slider-settings {
    border: 1px solid #ddd;
    border-radius: 4px;
    padding: 10px 8px;
    margin-bottom: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    transition: cubic-bezier(0.25, 0.46, 0.45, 0.94) 3s ease;
    background-color: #fff;
    overflow: hidden; /* Ensures content does not overflow */
}

.slider-settings.expanded {
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
}

.slider-header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    cursor: pointer; /* Indicates that the header is clickable */
}

.slider-title {
    font-weight: bold;
    flex: 1;
}

.slider-actions {
    display: flex;
    align-items: center;
}

.delete-button,
.expand-button {
    margin-left: 10px;
}

.separator {
    margin-left: 10px;
    margin-right: 10px;
    font-size: 20px; /* Adjust size to fit your design */
}

.slider-content {
    margin-top: 10px;
    padding-top: 10px;
    border-top: 1px solid #ddd;
    max-height: 0; /* Initial hidden state */
    overflow: hidden; /* Hide overflow during transition */
    transition: max-height 0.3s ease; /* Smooth transition */
}

.slider-settings.expanded .slider-content {
    max-height: 300px; /* Adjust based on content height */
}

.slider-content .slider-heading-text,
.slider-content .slider-paragraph-text {
    width: 95%; /* Ensures that TextControl fits within the container */
    box-sizing: border-box;
    margin-left: 10px;
    margin-right: 10px; /* Include padding and border in the width calculation */
}

.slider-content .slider-heading-text {
    margin-bottom: 10px; /* Space between heading and paragraph fields */
}

.slider-content .components-base-control {
    margin-bottom: 10px; /* General margin for components inside slider-content */
}

.slider-tabs {
    display: flex;
    margin-top: 10px;
    border-top: 1px solid #ddd;
    padding-top: 10px;
}

.tab-button {
    background-color: #f5f5f5;
    border: 1px solid #ddd;
    border-radius: 4px;
    padding: 5px 10px;
    margin-right: 10px;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.tab-button.active {
    background-color: #0073aa;
    color: #fff;
    border-color: #0073aa;
}

.slider-buttons {
    margin-top: 10px;
    padding-top: 10px;
    border-top: 1px solid #ddd;
}

.button-option {
    margin-bottom: 20px; /* Space between button options */
}

.slider-button-text,
.slider-button-url {
    width: calc(100% - 20px); /* Ensures that TextControl fits within the container */
    box-sizing: border-box;
    margin-bottom: 10px; /* Space between fields */
}

.slider-button-text {
    margin-bottom: 5px; /* Space between button text and URL fields */
}


.slider-style {
    margin-top: 10px;
    padding-top: 10px;
    border-top: 1px solid #ddd;
}

.slider-style {
    .section-title {
        font-weight: bold;
        margin-bottom: 10px;
        background-color: black;
        color: white;
        font-size: 15px;
        padding: 5px 5px;
    }
    .control-group {
        margin-bottom: 15px;
            .control-label {
                display: block;
                margin-bottom: 5px;
                font-size: 14px;
            }
        }
}


.add-new-slide-button {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    background-color: #007cba; /* Primary color */
    color: #fff; /* Text color */
    border: none;
    border-radius: 4px;
    padding: 8px 16px;
    font-size: 14px;
    font-weight: bold;
    cursor: pointer;
    transition: background-color 0.2s ease, transform 0.2s ease;
    margin-top: 16px; /* Space above the button */
}

.add-new-slide-button:hover {
    background-color: #005a87; /* Darker shade for hover effect */
}

.add-new-slide-button:focus {
    outline: none;
    box-shadow: 0 0 0 2px rgba(0, 123, 255, 0.5); /* Focus outline */
}

.add-new-slide-button:active {
    transform: scale(0.98); /* Slightly shrink the button on click */
}



.new-block{
    width: 200px;
    height: 200px;
    background-color: blue;
}
