/* Heading styling within each slide */
.slider-slide h1 {
    font-size: 1rem;
    color: #333; /* Text color */
}

/* Paragraph styling within each slide */
.slider-slide p {
    font-size: 1rem;
    color: #666; /* Text color */
    line-height: 1;
}

/* Container for slider buttons */
.slider-buttons {
    align-items: center;
    justify-content: center;
    display: flex;
    gap: 5px; /* Space between buttons */
}

/* General button styling */
.slider-button {
    display: inline-block;
    padding: 10px 20px;
    font-size: 1rem;
    text-decoration: none !important;
    border-radius: 5px; /* Rounded corners for the buttons */
    color: #fff; /* Button text color */
    transition: background-color 0.3s ease; /* Smooth background color transition */
}

/* Button 1 specific styling */
.slider-button-1 {
    text-decoration: none !important;
    background-color: #007cba; /* Background color for button 1 */
}

.slider-button-1:hover {
    
    background-color: #005a8d; /* Background color on hover */
}

/* Button 2 specific styling */
.slider-button-2 {
    text-decoration: none !important;
    background-color: #f15b5b; /* Background color for button 2 */
}

.slider-button-2:hover {
    background-color: #c43a3a; /* Background color on hover */
}


.slider-slide {
    overflow: hidden; // Ensure content does not overflow outside of the slide
    height: 300px; // Example height, adjust as needed
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: gray;
}

.slick-slide {
    height: auto !important;
}
